/* Home.css */
@keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(-20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  body {
    margin: 0;
    /* font-family: "Maven Pro", sans-serif; */
    font-family: monospace, monospace;

    font-weight: lighter;
    color: rgb(255, 255, 255);
    background-color: white;
    text-decoration: none;
  }
  
  .home-container {
    display: flex;
    height: 100vh;
    margin: 0 0;
    animation: fadeIn 2s ease-in-out forwards;
  }
  
  .h1-container {
    flex: 70%;
    justify-content: center;
    text-align: center;
  }
  
  .trinidad-text {
    font-size: 5rem;
    margin: 15rem 7rem 0 3rem;
    transition: transform 0.9s ease-in-out;
    z-index: 100;
    letter-spacing: -1px;
  }
  
  .trinidad-text:hover {
    color: rgb(255, 255, 255, 0.199);
    transform: scale(1.1);
  }

  
  .image9 {
    position: absolute;
    top: -6rem;
    left: 39rem;
    opacity: 90%;
    z-index: -3;
    
  }
  

  .plusSign-container {
    flex: 30%;
    align-items: center;
    padding: 16rem 3rem 0 3rem;
    text-align: right;
    text-decoration: none;
  }


  @media (max-width: 1024px) {
    .home-container {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .h1-container,
    .plusSign-container {
        flex: unset;
        width: 100%;
        text-align: center;
        padding: 2rem;
    }
    .trinidad-text {
        font-size: 5rem;
        margin: 5rem 0;
    }
    .plusSign-container {
        padding: 2rem;
    }
}

@media (max-width: 768px) {
    .trinidad-text {
        font-size: 2.5rem;
        margin: 3rem 0;
    }
    .plusSign-container {
        padding: 1.5rem;
    }
}

@media (max-width: 480px) {
    .trinidad-text {
        font-size: 4rem;
        margin: 2rem 0;
    }
    .plusSign-container {
        padding: 1rem;
    }
}
